<h3 className="">{"Validation"}</h3>
{"\n"}
<p className=""><code className="">{"<mwc-textarea>"}</code>
{" follows the basic "}
<code className="">{"<input>"}</code>
{" "}
<a href="https://developer.mozilla.org/en-US/docs/Web/Guide/HTML/HTML5/Constraint_validation" className="">{"constraint validation model"}</a>
{". It exposes:"}</p>
{"\n"}
<ul className="">{"\n"}
<li className=""><code className="">{"required"}</code></li>
{"\n"}
<li className=""><code className="">{"maxLength"}</code></li>
{"\n"}
<li className=""><code className="">{"validity"}</code></li>
{"\n"}
<li className=""><code className="">{"willValidate"}</code></li>
{"\n"}
<li className=""><code className="">{"checkValidity()"}</code></li>
{"\n"}
<li className=""><code className="">{"reportValidity()"}</code></li>
{"\n"}
<li className=""><code className="">{"setCustomValidity(message)"}</code></li>
{"\n"}</ul>
{"\n"}
<p className="">{"Additionally, it implements more features such as:"}</p>
{"\n"}
<ul className="">{"\n"}
<li className=""><code className="">{"validationMessage"}</code></li>
{"\n"}
<li className=""><code className="">{"validateOnInitialRender"}</code></li>
{"\n"}
<li className="">{"and "}
<code className="">{"validityTransform"}</code></li>
{"\n"}</ul>
{"\n"}
<p className="">{"By default, "}
<code className="">{"<mwc-textarea>"}</code>
{" will report validation on "}
<code className="">{"blur"}</code>
{"."}</p>
{"\n"}
<h4 className="">{"Custom validation logic"}</h4>
