<p className="">{"In this example we first check the native validity which is invalid due to the\nmaxlength (the value is "}
<code className="">{"doggos"}</code>
{" which is a string longer than 5). The value\nincludes "}
<code className="">{"dog"}</code>
{", thus we make it valid and undo the "}
<code className="">{"tooLong"}</code>
{" error in the\n"}
<code className="">{"ValidityState"}</code>
{" by returning a "}
<code className="">{"Partial<ValidityState>"}</code>
{" that indicates what to\nchange in the initial validity."}</p>
{"\n"}
<p className="">{"In this example, we also skip an expensive validity check by short-circuiting\nthe validation by checking the native validation."}</p>
{"\n"}
<p className=""><em className="">{"Note:"}</em>
{" the UI will only update as valid / invalid by checking the "}
<code className="">{"valid"}</code>
{"\nproperty of the transformed "}
<code className="">{"ValidityState"}</code>
{"."}</p>
